@import './palette';

.sider {
  width: 320px;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  padding-left: 50px;
  padding-right: 50px;
  padding-top: 90px;
  background: #fff;
  transition: all, .3s;
  z-index: 5;
  @media (max-width: 1128px) {
    transform: translateX(-100%);
    transition: all, .3s;
  }
  &.isActive {
    @media (max-width: 1128px) {
      transform: translateX(0);
      transition: all, .3s;
    }
  }
  &__item {
    margin: 15px 0;
    display: block;
    text-decoration: none;
    span {
      font-size: 27px;
      font-weight: 300;
      text-transform: uppercase;
      color: #000;
    }
    &.isActive {
      span {
        color: grey;
      }

    }
  }
  &__menuBtn {
    display: none;
    @media (max-width: 1128px) {
      display: block;
      position: fixed;
      top: 5px;
      left: 5px;
      z-index: 100;
      padding: 5px 9px;
      cursor: pointer;
    }
    svg {
      font-size: 32px;
    }
    &.isActive {
      color: red;
    }
  }
}
